import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Note from './components/Note'
import loginService from './services/login' 






const App = () => {
  const [notes, setNotes] = useState([]) 
  const [newNote, setNewNote] = useState('')
  const [showAll, setShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState(null)
  const [username, setUsername] = useState('') 
  const [password, setPassword] = useState('') 
  const [user, setUser] = useState(null) //{'name': 'zafer'}



  useEffect(() => {
    console.log('effect')
    axios
      .get('/notes')
      .then(response => {
        console.log('promise fulfilled')
        setNotes(response.data)
      })
  }, [])

  console.log('render', notes.length, 'notes')






//----------------------------------------------------------------------

  let token = null

  const setToken = newToken => {
  token = `bearer ${newToken}`
}





  const addNote = (event) => {
    event.preventDefault()
  
     const noteObject = {
      content: newNote,
      date: new Date().toISOString(),
      important: Math.random() > 0.5, }




      const config = {
    headers: { Authorization: token },
  }


    axios
    .post('/notes', noteObject, config )
    .then(res => setNotes(notes.concat(res.data), setNewNote('')))
    .catch(err => setErrorMessage(`Posting Note ='${noteObject.content}' has not done `), setTimeout(() => {setErrorMessage('')}, 5000) )
  }




//-----------------------------------------------------------------------
  const handleNoteChange = (event) => {
    setNewNote(event.target.value)
  }


//------------------------------------------------------------------------
  const notesToShow = showAll
    ? notes
    : notes.filter(note => note.important)





//------------------------------------------------------------------------
const Alert = () => {

 if(errorMessage === ('')) {

  return <div></div>
 
 }
 
 else {

return (
    <div className='error'>
      
      {errorMessage}
    
      
    </div>
  )
}
}

//---------------------------------------------------------------------------

const handleLogin = async (event) => {
    event.preventDefault()
    
  try {
      const user = await loginService.login({username, password,})

      setToken(user.token)
      setUser(user)
      setUsername('')
      setPassword('')
    } 
    
    
    catch (exception)
     {
      setErrorMessage('Wrong credentials')
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    }
  }



//-------------------------------------------------------------------------



const loginForm = () => (
        <form onSubmit={handleLogin}>

        <div>
          username:
            &nbsp;
            <input
            type="text"
            value={username}
            name="Username"
            onChange={({ target }) => setUsername(target.value)}
        />
        </div>
        <br/>
        <div>
           password:
            &nbsp;
            <input
            type="password"
            value={password}
            name="Password"
            onChange={({ target }) => setPassword(target.value)}
          />
        </div>
        <button type="submit">login</button>
      </form>
      )



//--------------------------------------------------------------------------

 const noteForm = () => (
      <form onSubmit={addNote}>
        <input
          value={newNote}
          onChange={handleNoteChange}
        />
        <button type="submit">save</button>
      </form>   
   

    )

  

 



//------------------------------------------------------------------------
  return (
    <div>
      <h1>Notes</h1>
      <Alert/>

      
        
      
      {user === null ?
         loginForm() :       
         <div>
           
        <p><button onClick={() => {setUser(null)}}>Log out</button>&nbsp;&nbsp;&nbsp;{user.name} <span>Logged in</span> </p>
        {noteForm()}
        </div>
       }

      <ul>
        {notesToShow.map((note, i) => 
          <Note key={i} note={note} />
        )}
      </ul>

          <br/>
          <div>
        <button onClick={() => setShowAll(!showAll)}>
          show {showAll ? 'important' : 'all' }
        </button>
      </div>   
 </div>


       



  )
}

export default App;
